<template>
    <div class="related-ticket">
        <jb-search-select
            ref="search"
            class="search-select-style"
            @on-change="handleSearch"
            :parse-url="false"
            :data="searchSelect"
            :placeholder="'搜索文件源别名...'" />
        <render-list
            ref="fileSourcelist"
            :data-source="fetchFileSourceList"
            :search-control="() => $refs.search">
            <bk-table-column width="60">
                <template slot-scope="{ row }">
                    <span v-html="row.publicFlagHtml" />
                </template>
            </bk-table-column>
            <bk-table-column
                :label="'文件源别名'"
                sortable
                align="left"
                show-overflow-tooltip>
                <template slot-scope="{ row }">
                    <auth-router-link
                        v-if="row.isAvailable"
                        auth="file_source/view"
                        :permission="row.canView"
                        :resource-id="row.id"
                        :to="{
                            name: 'fileList',
                            query: {
                                fileSourceId: 4 || row.id,
                            },
                        }"
                        target="_blank">
                        {{ row.alias }}
                    </auth-router-link>
                    <span
                        v-else
                        v-bk-tooltips="'接入点异常，暂时不可用'">
                        <bk-button disabled text>{{ row.alias }}</bk-button>
                    </span>
                </template>
            </bk-table-column>
            <bk-table-column
                :label="'文件源标识'"
                sortable
                align="left"
                width="160"
                show-overflow-tooltip
                prop="code" />
            <bk-table-column
                width="80"
                :label="'状态'">
                <template slot-scope="{ row }">
                    <Icon :type="row.statusIcon" svg />
                    {{ row.statusText }}
                </template>
            </bk-table-column>
            <bk-table-column
                width="120"
                :label="'更新人'"
                prop="lastModifyUser" />
            <bk-table-column
                width="180"
                :label="'更新时间'"
                prop="lastModifyTime" />
        </render-list>
    </div>
</template>
<script>
       import FileManageService from '@service/file-source-manage';
    import JbSearchSelect from '@components/jb-search-select';
    import RenderList from '@components/render-list';

    export default {
        name: 'RelatedTicket',
        components: {
            JbSearchSelect,
            RenderList,
        },
        props: {
            credentialId: {
                type: String,
            },
        },
        data () {
            return {
                sourceFileList: [{},{},{}],
            };
        },
        mounted () {
            this.fetchData();
        },
        created () {
            this.fetchFileSourceList = FileManageService.fetchFileSourceList;
            this.searchSelect = [
                {
                    name: '文件源别名',
                    id: 'alias',
                },
            ];
        },
        methods: {
            /**
             * @desc 获取被引用文件源列表
             */
            fetchData () {
                this.$refs.fileSourcelist.$emit('onFetch', {
                    ...this.searchParams,
                    credentialId: this.credentialId,
                });
            },
            /**
             * @desc 过滤表格数据
             * @param {Array} payload 用户输入的过滤数据
             *
             * 重新拉取数据
             */
            handleSearch (payload) {
                this.searchParams = payload;
                this.fetchData();
            },
        },
    };
</script>
<style lang="postcss">
    .related-ticket {
        .search-select-style {
            width: 480px;
            margin-bottom: 20px;
        }
    }
</style>
