<template>
    <jb-form ref="form" :model="formData" :rules="rules" form-type="vertical">
        <jb-form-item :label="'密码'" required property="value1">
            <bk-input v-model="formData.value1" type="password" />
        </jb-form-item>
        <jb-form-item :label="'描述'">
            <bk-input
                v-model="formData.description"
                type="textarea"
                maxlength="100" />
        </jb-form-item>
    </jb-form>
</template>

<script>
   
    export default {
        name: 'Password',
        props: {
            data: {
                type: Object,
                default: () => ({}),
            },
            type: {
                type: String,
                default: '',
            },
        },
        data () {
            return {
                formData: {
                    value1: this.data.value1,
                    description: this.data.description,
                },
            };
        },
        created () {
            if (this.type !== 'PASSWORD') {
                this.formData.value1 = '';
            }
            this.rules = {
                value1: [
                    {
                        required: true,
                        message: '密码必填',
                        trigger: 'blur',
                    },
                ],
            };
        },
        methods: {
            /**
             * @desc 校验表单
             *
             * 校验成功传递表单数据到父组件
             */
            getData () {
                return this.$refs.form.validate()
                    .then(validator => this.formData, validator => Promise.reject(validator.content));
            },
        },
    };
</script>
